import { FC, HTMLAttributes, useMemo } from "react";
import styles from "./WidgetLibraryTile.module.scss";
import * as icons from "../../../assets/icons";
import { AiOutlineCheck as Check } from "react-icons/ai";
import cx from "classnames";
import widgets from "../../../features/widgets/widgets";

interface Props extends HTMLAttributes<HTMLDivElement> {
  title: string;
  widgetIds: string[];
  onQuestionClicked: () => void;
  isChecked: boolean;
  onCheckboxClicked: () => void;
  isAvailable: boolean;
}

export const WidgetLibraryTile: FC<Props> = ({
  className,
  title: passedTitle,
  widgetIds,
  onQuestionClicked,
  isChecked,
  onCheckboxClicked,
  isAvailable,
  ...props
}: Props) => {
  const title =
    passedTitle ??
    (widgetIds[0] ? widgets.entities[widgetIds[0]]?.name ?? "" : "");

  const renderIcon = (widgetId: string, key?: number) => {
    const widgetIcon = widgets.entities[widgetId]?.icon;
    if (widgetIcon) {
      const Icon = icons[widgetIcon];
      return <Icon key={key} />;
    }
  };

  const isAnyWidgetExists = useMemo(() => {
    return widgetIds.some((id) => !!widgets.entities[id]);
  }, [widgetIds]);

  return isAnyWidgetExists ? (
    <div
      className={cx(
        styles[widgetIds.length > 1 ? "template" : "widget"],
        className
      )}
      {...props}
    >
      <span>
        {isAvailable ? (
          <div className={styles.buttonGroup}>
            <button
              className={styles.buttonGroupShowInfo}
              onClick={isAvailable ? () => onQuestionClicked() : undefined}
            >
              ?
            </button>
            <button
              className={`${styles.buttonGroupCheck} ${
                isChecked ? styles.checked : ""
              }`}
              onClick={isAvailable ? () => onCheckboxClicked() : undefined}
            >
              <Check size={11} />
            </button>
          </div>
        ) : (
          <div className={styles.unavailable}>
            <icons.Lock size={12} />
            <p>Available at upgrade</p>
          </div>
        )}
        <div
          className={styles.info}
          style={isAvailable ? { opacity: "1" } : { opacity: "0.25" }}
        >
          {widgetIds.length > 1 ? (
            <>
              <p>{title}</p>
              <div className={styles.icons}>
                {widgetIds.map((id, i) => renderIcon(id, i))}
              </div>
            </>
          ) : (
            <>
              {widgetIds[0] && renderIcon(widgetIds[0])}
              <p>{title}</p>
            </>
          )}
        </div>
      </span>
    </div>
  ) : null;
};
