import { FC, useContext, useState, useMemo, useEffect } from "react";
import styles from "./WidgetLibraryPage.module.scss";
import context from "../../../context";
import { useSelector, useDispatch } from "react-redux";
import { fetchAllDashboards } from "../dashboardsSlice";
import { WidgetLibraryTile } from "./WidgetLibraryTile";
import { WidgetLibraryWidgetsPreview } from "./WidgetLibraryWidgetsPreview";
import { WidgetLibraryCreateDashboard } from "./WidgetLibraryCreateDashboard";
import { Preloader, Input, Select, Label } from "../../../components";
import { Magnifier, Close } from "../../../assets/icons";
import { withError } from "../../../hocs";
import { selectCompanySubscriptionPlan } from "../../../features/subscriptionPlans/subscriptionPlansSlice";
import widgets from "../../../features/widgets/widgets";
import widgetTemplates from "../../../features/widgets/widgetTemplates";
import { useImmer } from "use-immer";
import { useTemporaryErrors } from "../../../hooks";

const SelectWithError = withError(Select);

type Tile = {
  title: string;
  description: string;
  widgetIds: string[];
  isAvailable: boolean;
  isHidden?: boolean;
};

const WidgetLibraryPage: FC = () => {
  const { setModalElement } = useContext(context);
  const dispatch = useDispatch();
  const { errors, setErrors } = useTemporaryErrors();

  // searches
  const searchOptions = useSelector(({ searches }: Store.State) =>
    searches.map(({ id, name }) => ({ value: id, label: name }))
  );

  const [searchId, setSearchId] = useState("");

  // dashboards
  const [isPrivate, setIsPrivate] = useState(false);

  const dashboardsStatus = useSelector(
    ({ dashboards }: RootState) => dashboards.status
  );

  useEffect(() => {
    if (dashboardsStatus === "idle") {
      dispatch(fetchAllDashboards());
    }
  }, [dashboardsStatus, dispatch]);

  // tiles
  const [filter, setFilter] = useState("");

  const companySubscriptionPlan = useSelector(selectCompanySubscriptionPlan);

  const tiles: Tile[] = useMemo(() => {
    const availableWidgetIds = companySubscriptionPlan?.availableWidgets ?? [];

    function getAvailabilityStatus(ids: Tile["widgetIds"]) {
      return ids.every((id) => availableWidgetIds.includes(id));
    }

    return [
      ...widgetTemplates
        .map((template) => ({
          ...template,
          isAvailable: getAvailabilityStatus(template.widgetIds),
        }))
        .sort((a, b) => +b.isAvailable - +a.isAvailable),
      ...widgets.ids
        .map((id) => ({
          title: widgets.entities[id]?.name ?? "",
          description: widgets.entities[id]?.description ?? "",
          widgetIds: [id],
          isAvailable: getAvailabilityStatus([id]),
          isHidden: !widgets.entities[id]?.name
            .toLowerCase()
            .includes(filter.toLowerCase()),
        }))
        .sort((a, b) => +b.isAvailable - +a.isAvailable)
        .sort((a, b) => +a.isHidden - +b.isHidden),
    ];
  }, [companySubscriptionPlan, filter]);

  const [hoveredTileIndex, setHoveredTileIndex] = useState(-1);

  const hoveredTile = tiles[hoveredTileIndex];

  const [selectedWidgetIds, updateSelectedWidgetIds] = useImmer<{
    [widgetId: string]: true;
  }>({});

  // const selectedWidgetIdsList = useMemo(() => {
  //   return widgets.ids.filter((id) => selectedWidgetIds[id]);
  // }, [selectedWidgetIds]);

  const [
    isCreateDashboardSectionShown,
    setIsCreateDashboardSectionShown,
  ] = useState(false);

  useEffect(() => {
    if (
      isCreateDashboardSectionShown &&
      !Object.keys(selectedWidgetIds).length
    ) {
      setIsCreateDashboardSectionShown(false);
    }
  }, [isCreateDashboardSectionShown, selectedWidgetIds]);

  function showPreviewModal() {
    if (hoveredTile) {
      setModalElement(
        <div className={styles.modal}>
          <WidgetLibraryWidgetsPreview
            title={hoveredTile.title}
            description={hoveredTile.description}
            widgetIds={hoveredTile.widgetIds}
            isAvailable={hoveredTile.isAvailable}
          />
        </div>
      );
    }
  }

  function getIsEachWidgetOfTileChecked(widgetIds: string[]) {
    return widgetIds.every((id) => selectedWidgetIds[id]);
  }

  function checkEachWidgetOfTile(widgetIds: string[]) {
    widgetIds.forEach((id) =>
      updateSelectedWidgetIds((draft) => void (draft[id] = true))
    );
  }

  function uncheckEachWidgetOfTile(widgetIds: string[]) {
    widgetIds.forEach((id) =>
      updateSelectedWidgetIds((draft) => void delete draft[id])
    );
  }

  function onSaveClicked() {
    if (!searchId) {
      setErrors({
        searchId: "Required",
      });
      return;
    }

    if (window.innerWidth >= 992) {
      setIsCreateDashboardSectionShown(true);
    } else {
      setIsCreateDashboardSectionShown(false);
      setModalElement(
        <div className={styles.modal}>
          <WidgetLibraryCreateDashboard
            isPrivate={isPrivate}
            searchId={searchId}
            selectedWidgetIds={selectedWidgetIds}
          />
        </div>
      );
    }
  }

  if (dashboardsStatus === "loading") {
    return <Preloader modifier="inner" />;
  }

  return (
    <div className={styles.widgetLibraryPage}>
      <div className={styles.title}>Widget library</div>
      <div className={styles.selectsWrapper}>
        <div className={styles.selectWrapper}>
          <Label leftText="Select your dashboard type" />
          <SelectWithError
            options={[
              { value: "", label: "Public" },
              { value: " ", label: "Private" },
            ]}
            value={isPrivate ? " " : ""}
            changeHandler={(value) => setIsPrivate(Boolean(value))}
          />
        </div>
        <div className={styles.selectWrapper}>
          <Label leftText="Select your search" />
          <SelectWithError
            options={searchOptions}
            value={searchId}
            changeHandler={(value) => setSearchId(value)}
            error={errors.searchId}
          />
        </div>
      </div>
      <div className={styles.subtitles}>
        <div>Here you select what widgets to use to visualise your data.</div>
        {!!Object.keys(selectedWidgetIds).length && (
          <div>
            When you’re done with your selection, save your choices.
            <button className={styles.button} onClick={() => onSaveClicked()}>
              Save
            </button>
          </div>
        )}
      </div>
      <div className={styles.сontent}>
        <div className={styles.main}>
          <div>
            <div className={styles.note}>
              Ready made templates packages (choosing all three are recommended
              to reach a full coverage)
            </div>
            <div className={styles.templatesWrapper}>
              {tiles.slice(0, 3).map(({ title, widgetIds, isAvailable }, i) => {
                const isEveryWidgetChecked = getIsEachWidgetOfTileChecked(
                  widgetIds
                );
                return (
                  <WidgetLibraryTile
                    title={title}
                    widgetIds={widgetIds}
                    onQuestionClicked={() => showPreviewModal()}
                    isChecked={isEveryWidgetChecked}
                    onCheckboxClicked={() =>
                      isEveryWidgetChecked
                        ? uncheckEachWidgetOfTile(widgetIds)
                        : checkEachWidgetOfTile(widgetIds)
                    }
                    isAvailable={isAvailable}
                    onMouseEnter={() => setHoveredTileIndex(i)}
                    onMouseLeave={() => setHoveredTileIndex(-1)}
                    key={i}
                  />
                );
              })}
            </div>
          </div>
          <div>
            <div className={styles.note}>Or choose widget of your liking</div>
            <div className={styles.filter}>
              <Input
                className={styles.filterInput}
                value={filter}
                changeHandler={(value) => setFilter(value)}
                placeholder="Search for widget..."
              />
              <Magnifier
                className={styles.filterIcon}
                size={20}
                opacity={filter ? 1 : 0.25}
              />
            </div>
            <div className={styles.widgetsWrapper}>
              {tiles
                .slice(3)
                .map(({ title, widgetIds, isAvailable, isHidden }, i) => {
                  const isEveryWidgetChecked = getIsEachWidgetOfTileChecked(
                    widgetIds
                  );

                  return (
                    <WidgetLibraryTile
                      style={{ ...(isHidden && { visibility: "hidden" }) }}
                      title={title}
                      widgetIds={widgetIds}
                      onQuestionClicked={() => showPreviewModal()}
                      isChecked={isEveryWidgetChecked}
                      onCheckboxClicked={() =>
                        isEveryWidgetChecked
                          ? uncheckEachWidgetOfTile(widgetIds)
                          : checkEachWidgetOfTile(widgetIds)
                      }
                      isAvailable={isAvailable}
                      onMouseEnter={() => setHoveredTileIndex(i + 3)}
                      onMouseLeave={() => setHoveredTileIndex(-1)}
                      key={i}
                    />
                  );
                })}
            </div>
          </div>
        </div>
        <div className={styles.aside}>
          {!!hoveredTile && !isCreateDashboardSectionShown && (
            <div className={styles.asideSection}>
              <WidgetLibraryWidgetsPreview
                title={hoveredTile.title}
                description={hoveredTile.description}
                widgetIds={hoveredTile.widgetIds}
                isAvailable={hoveredTile.isAvailable}
              />
            </div>
          )}
          {isCreateDashboardSectionShown && (
            <div className={styles.asideSection}>
              <button
                className={styles.closeButton}
                onClick={() => setIsCreateDashboardSectionShown(false)}
              >
                <Close size={28} />
              </button>
              <WidgetLibraryCreateDashboard
                isPrivate={isPrivate}
                searchId={searchId}
                selectedWidgetIds={selectedWidgetIds}
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default WidgetLibraryPage;
