import { FC, useMemo } from "react";
import styles from "./WidgetLibraryWidgetsPreview.module.scss";
import { FadeInImage } from "../../../components";
import * as icons from "../../../assets/icons";
import widgets from "../../../features/widgets/widgets";

type Props = {
  title: string;
  description: string;
  widgetIds: string[];
  isAvailable: boolean;
};

export const WidgetLibraryWidgetsPreview: FC<Props> = ({
  title,
  description,
  widgetIds,
  isAvailable,
}: Props) => {
  const renderIcon = (widgetId: string, key?: number) => {
    const widgetIcon = widgets.entities[widgetId]?.icon;
    if (widgetIcon) {
      const Icon = icons[widgetIcon];
      return <Icon key={key} />;
    }
  };

  const relatedWidgetIds = useMemo(() => {
    if (widgetIds.length === 1 && widgetIds[0]) {
      return widgets.entities[widgetIds[0]]?.relatedWidgetIds ?? [];
    }

    return [];
  }, [widgetIds]);

  return (
    <>
      <div className={styles.header}>
        <div>{widgetIds.map((id, i) => renderIcon(id, i))}</div>
        <span>{title}</span>
        {!isAvailable && (
          <div className={styles.ifNotAvaliable}>
            <icons.Lock size={12} />
            <p>Available at upgrade</p>
          </div>
        )}
      </div>
      <div className={styles.images}>
        {widgetIds.map((id, i) => {
          const name = widgets.entities[id]?.name;
          const image = widgets.entities[id]?.image;

          return name && image ? (
            <div key={i}>
              {widgetIds.length > 1 && (
                <div className={styles.imageTitle}>{name}</div>
              )}
              <FadeInImage className={styles.image} name={image} key={i} />
            </div>
          ) : null;
        })}
      </div>
      <div className={styles.description}>{description}</div>
      {!!relatedWidgetIds.length && (
        <div className={styles.relatedWidgets}>
          <div>Works well together with:</div>
          <div>
            {relatedWidgetIds.map((id, i) => (
              <div key={i}>
                {renderIcon(id, i)}
                <p>{widgets.entities[id]?.name}</p>
              </div>
            ))}
          </div>
        </div>
      )}
    </>
  );
};
