import { FC, useContext, useState, useMemo } from "react";
import styles from "./WidgetLibraryCreateDashboard.module.scss";
import { FadeInImage, Label, Input } from "../../../components";
import { Chevron } from "../../../assets/icons";
import Slider from "react-slick";
import widgets from "../../../features/widgets/widgets";
import { withError } from "../../../hocs";
import { useTemporaryErrors } from "../../../hooks";
// import { useImmer } from "use-immer";
import { useHistory } from "react-router-dom";
import context from "../../../context";
import { useSelector, useDispatch } from "react-redux";
import { selectAllDashboards } from "../dashboardsSlice";
import { unwrapResult } from "@reduxjs/toolkit";
import { addOneDashboard } from "../dashboardsSlice";
import { notify } from "react-notify-toast";

const InputWithError = withError(Input);

type Props = {
  isPrivate: boolean;
  searchId: string;
  selectedWidgetIds: { [widgetId: string]: true };
};

export const WidgetLibraryCreateDashboard: FC<Props> = (props) => {
  const { setIsGlobalPreloaderShown, setModalElement } = useContext(context);
  const history = useHistory();

  const allDashboards = useSelector(selectAllDashboards);
  console.log(allDashboards);

  // const [selectedDashboardIds, updateSelectedDashboardIds] = useImmer<{
  //   [dashboardId: string]: true;
  // }>({});

  // function checkEachDashboardOfFolder(widgetIds: string[]) {
  //   widgetIds.forEach((id) =>
  //   updateSelectedDashboardIds((draft) => void (draft[id] = true))
  //   );
  // }

  // function uncheckEachDashboardOfFolder(widgetIds: string[]) {
  //   widgetIds.forEach((id) =>
  //   updateSelectedDashboardIds((draft) => void delete draft[id])
  //   );
  // }

  const [name, setName] = useState("");

  const dispatch: AppDispatch = useDispatch();

  const { errors, setErrors } = useTemporaryErrors();

  const selectedWidgetIdsList = useMemo(() => {
    return widgets.ids.filter((id) => props.selectedWidgetIds[id]);
  }, [props.selectedWidgetIds]);

  const slides = useMemo(() => {
    type Slide = Pick<ValueOf<typeof widgets.entities>, "name" | "image">;

    return selectedWidgetIdsList.reduce<Slide[]>((acc, id) => {
      const name = widgets.entities[id]?.name;
      const image = widgets.entities[id]?.image;

      if (name && image) {
        acc.push({
          name,
          image,
        });
      }

      return acc;
    }, []);
  }, [selectedWidgetIdsList]);

  const sliderProps = useMemo(() => {
    return {
      dots: false,
      infinite: true,
      arrows: true,
      speed: 500,
      slidesToShow: slides.length >= 3 ? 3 : slides.length >= 2 ? 2 : 1,
      slidesToScroll: 1,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
      responsive: [
        {
          breakpoint: 1399,
          settings: {
            slidesToShow: slides.length >= 2 ? 2 : 1,
          },
        },
        {
          breakpoint: 1199,
          settings: {
            slidesToShow: 1,
          },
        },
        {
          breakpoint: 991,
          settings: {
            slidesToShow: 2,
          },
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: 1,
          },
        },
      ],
    };
  }, [slides]);

  async function onDoneClicked() {
    const trimmedName = name.trim();

    if (!trimmedName) {
      setErrors({
        name: "Required",
      });
      return;
    }

    setIsGlobalPreloaderShown(true);
    try {
      const result = await dispatch(
        addOneDashboard({
          name: trimmedName,
          isPrivate: props.isPrivate,
          searchId: props.searchId,
          folderId: null,
          widgetIds: selectedWidgetIdsList,
          configs: {},
        })
      );
      const createdDashboard = unwrapResult(result);
      setModalElement(null);
      notify.show("Successfully saved", "success");
      history.push("/home/dashboards/" + createdDashboard.id);
    } catch (error) {
      console.dir(error);
      notify.show("Oops, something went wrong", "error");
    }
    setIsGlobalPreloaderShown(false);
  }

  return (
    <div className={styles.widgetLibraryCreateDashboard}>
      <div className={styles.title}>Create a dashboard</div>
      {!!slides.length && (
        <div>
          <div className={styles.note}>
            You have chosen the widgets below for your dashboard. You can always
            add on later.
          </div>
          <Slider className={styles.slider} {...sliderProps}>
            {slides.map(({ name, image }, i) => (
              <div className={styles.slide} key={i}>
                <div className={styles.slideText}>{i + 1}</div>
                <div className={styles.slideImageWrapper}>
                  <FadeInImage className={styles.slideImage} name={image} />
                </div>
                <div className={styles.slideText}>{name}</div>
              </div>
            ))}
          </Slider>
        </div>
      )}
      {!!allDashboards.length && (
        <div>
          <div className={styles.note}>
            If you like, you can add these widgets to already existing
            dashboards.
          </div>
          {/* <div className={styles.wrapper}>
          {ExistingDashbordsData.map((dashbord, index) => (
            <ExistingDashbords key={index} data={dashbord} />
          ))}
        </div> */}
        </div>
      )}
      <div>
        <Label leftText="Name your dashboard" />
        <div className={styles.inputWrapper}>
          <InputWithError
            className={styles.input}
            value={name}
            changeHandler={(value) => setName(value)}
            placeholder="My Brand X"
            error={errors.name}
          />
          <button className={styles.button} onClick={onDoneClicked}>
            Done
          </button>
        </div>
      </div>
    </div>
  );
};

function NextArrow(props: any) {
  const { style, onClick } = props;
  return (
    <div
      className={`${styles.slickArrow} ${styles.nextArrow}`}
      style={{ ...style }}
      onClick={onClick}
    >
      <Chevron size={10} />
    </div>
  );
}

function PrevArrow(props: any) {
  const { style, onClick } = props;
  return (
    <div
      className={`${styles.slickArrow} ${styles.prevArrow}`}
      style={{ ...style }}
      onClick={onClick}
    >
      <Chevron size={10} />
    </div>
  );
}
